import React from 'react'
import { StaticQuery, graphql, Link } from 'gatsby'
import { Location } from '@reach/router'

const PrimaryNavLinks = props => (
  <StaticQuery
    query={graphql`
      query {
        allSitePage(filter: { component: { regex: "/primary_nav/" } }) {
          edges {
            node {
              id
              path
              label
            }
          }
        }
      }
    `}
    render={data => {
      let { edges } = data.allSitePage
      return (
        <Location>
          {({ location }) => (
            <>
              <li key="home">
                <Link to="/">Home</Link>
              </li>
              {edges.map(({ node }) => (
                <li
                  key={node.id}
                  className={location.pathname === node.path ? 'active' : null}
                >
                  <Link to={node.path}>{node.label}</Link>
                </li>
              ))}
            </>
          )}
        </Location>
      )
    }}
  />
)

export default PrimaryNavLinks
